export default {
    name: "CaseNav",
    data() {
        return {
            currentTabIndex: 0,
        }
    },
    props:{
        categoryList:Array
    },
    methods:{
        changeTab(i){
            if (i!==this.currentTabIndex){
                this.currentTabIndex = i
                this.$emit('changeTab', i)
            }

        }
    }
}
